 .bodyWrapper {
     width: 100%;
     display: flex;
     flex-direction: column;
     margin-top: 5%;
     gap: 5rem;
 }

 .insight {
     width: 100%;
     height: auto;
     display: flex;
     flex-direction: column;
     justify-content: space-evenly;
     align-items: flex-start;
     gap: 30px;
     padding: 0px 20px;
     border-left: 3px solid #29df29
 }

 .insight .insight-article {
     width: 100%;
     display: flex;
     justify-content: space-between;
     align-items: center
 }

 .insight .insight-article .article1 {
     display: flex;
     flex-direction: column;
     flex: .5;
     gap: 20px
 }

 .insight .insight-article .article1 .insight-header {
     text-transform: capitalize;
     color: #29df29
 }

 .insight .insight-article .article1 .insight-text {
     color: gray;
     font-size: 16px
 }

 .insight .insight-article .article2 {
     display: flex
 }

 .insight .insight-article .article2 button {
     align-items: flex-end;
     justify-content: flex-start;
     display: flex;
     gap: 10px;
     cursor: pointer;
     position: relative;
     color: darkgray;
     text-decoration: none;
     border: none;
     background: transparent
 }

 .insight .insight-article .article2 button:hover {
     color: gray
 }

 .insight .insight-article .article2 button::before {
     content: "";
     display: flex;
     width: 40%;
     height: 2px;
     position: absolute;
     bottom: -10px;
     background-color: #29df29;
     transition: all 350ms ease-in-out
 }

 .insight .insight-article .article2 button:hover::before {
     width: 100%
 }

 .cardWrapper {
     display: flex;
     justify-content: space-between;
     gap: 50px
 }

 .cardWrapper .card {
     display: flex;
     flex: 1;
     height: auto;
     flex-direction: column;
     justify-content: space-between;
     align-items: flex-start;
     gap: 10px
 }

 .cardWrapper .card .card-imageWrapper {
     width: 100%;
     height: 300px;
     display: flex;
     overflow: hidden;
     border-radius: 10px;
     position: relative
 }

 .cardWrapper .card .card-imageWrapper img {
     height: 100%;
     width: 100%;
     display: flex;
     object-fit: cover
 }

 .cardWrapper .card .card-imageWrapper .card-title {
     position: absolute;
     width: 100%;
     justify-content: flex-end;
     align-items: center
 }

 .cardWrapper .card .card-imageWrapper .card-title p {
     display: flex;
     margin: 20px 30px;
     background-color: #FAFAFA;
     color: gray;
     border-radius: 5px;
     padding: 5px 10px;
     font-weight: 600;
     text-transform: capitalize;
     font-size: 12px;
     text-align: center;
     position: absolute;
     right: 0
 }

 .cardWrapper .card .card-date {
     display: flex;
     justify-content: flex-start;
     align-items: center;
     gap: 10px;
     text-transform: capitalize;
     font-size: 12px;
     color: darkgray
 }

 .cardWrapper .card .card-header {
     font-weight: 600;
     color: #444444
 }

 .cardWrapper .card .card-desc {
     font-size: 16px;
     color: #5e5e5e;
     font-weight: 500
 }

 .cardWrapper .card button {
     color: green;
     text-decoration: none;
     display: flex;
     justify-content: center;
     align-items: center;
     background-color: transparent;
     border: none;
     gap: 10px;
     cursor: pointer
 }

 .goals-cardWrapper {
     display: flex;
     justify-content: space-evenly;
     align-items: center;
     gap: 40px;
     height: auto;
     flex-wrap: wrap;

     .goal-card {
         display: flex;
         justify-content: space-between;
         align-items: flex-start;
         padding: 20px;
         gap: 10px;
         width: 500px;
         height: 120px;
         border: 1px solid gray;
         border-radius: 10px;
         overflow: hidden;

         .goalCard-iconWrapper {
             color: #29df29;
             font-size: 20px;
         }

         .goalCard-descWrapper {
             gap: 10px;

             h3 {
                 text-transform: capitalize;
                 color: #444444;
             }

             p {
                 color: darkgray;
                 font-size: 15px;
             }
         }
     }


 }

 .testimonialWrapper {
     display: flex;
     justify-content: space-between;
     align-items: flex-start;
     gap: 30px;
     height: auto;

     .t-cards {
         flex-direction: column;
         display: flex;
         color: gray;
         background-color: white;
         border: 1px solid gray;

         border-radius: 5px;
         padding: 15px 30px;
         gap: 10px;

         height: auto;
         flex: 1;
         flex-wrap: wrap;

         .t-ratings {
             display: flex;
             gap: 5px;
         }

         .t-comments {
             display: flex;
             font-size: 14px;
             height: 100px;
             margin: auto;
         }

         .t-userName {
             display: flex;
             text-transform: capitalize;

         }

         .userOccupation {
             font-size: 12px;
         }
     }


 }

 .joinWrapper {
     width: 100%;
     height: 300px;
     display: flex;
     justify-content: center;
     align-items: center;
     position: relative;
     border-radius: 20px;
     overflow: hidden
 }

 .joinWrapper .join-backgroundImage {
     height: 100%;
     width: 100%;
     margin: auto;
     display: flex;
     justify-content: center;
     align-items: center
 }

 .joinWrapper .join-backgroundImage img {
     flex: 1;
     display: flex;
     width: 100%;
     height: 100%;
     object-fit: cover
 }

 .joinWrapper .join-contentWrapper {
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     gap: 20px;
     height: 100%;
     width: 100%;
     position: absolute;
     margin: auto
 }

 .joinWrapper .join-contentWrapper h2 {
     font-size: 30px;
     color: white;
     text-transform: lowercase
 }

 .joinWrapper .join-contentWrapper p {
     font-size: 14px;
     color: white
 }

 .joinWrapper .join-contentWrapper .join-inputWrapper {
     display: flex;
     justify-content: center;
     align-items: center;
     width: auto;
     height: auto;
     border-radius: 10px;
     padding: 5px 20px
 }

 .joinWrapper .join-contentWrapper .join-inputWrapper .mailIcon {
     color: #222831;
     font-size: 18px
 }

 .joinWrapper .join-contentWrapper .join-inputWrapper input {
     flex: 1;
     outline: none;
     border: none;
     font-size: 12px;
     height: 100%;
     padding: 10px
 }

 .joinWrapper .join-contentWrapper .join-inputWrapper button {
     display: flex;
     height: auto;
     padding: 5px 15px;
     background-color: #222831;
     border: none;
     border-radius: 5px;
     font-size: 8px;
     justify-content: center;
     align-items: center;
     gap: 10px;
     color: white
 }

 .joinWrapper .join-contentWrapper .join-inputWrapper button .arrowIcon {
     font-size: 15px
 }

 .joinWrapper .join-contentWrapper .spam {
     display: flex;
     justify-content: center;
     align-items: center;
     backdrop-filter: blur(2px);
     padding: 10px
 }

 .joinWrapper .join-contentWrapper .spam p {
     gap: 10px;
     display: flex;
     align-items: center;
     justify-content: center;
     margin: auto;
     font-size: 15px
 }

 .joinWrapper .join-contentWrapper .spam p span {
     background-color: white;
     height: 40px;
     width: 40px;
     border-radius: 50%;
     display: flex;
     color: #29df29;
     justify-content: center;
     align-items: center;
     font-size: 15px
 }

